<template>
	<div class="animated fadeIn">
		<b-row>
			<b-col sm="12">
				<b-card>
					<b-form @submit="onSubmit" @reset="onReset" v-if="showSearch" inline class="mb-2">
						<b-input-group prepend="Name">
							<b-form-input v-model="search.name" type="text"></b-form-input>
						</b-input-group>
						<b-input-group prepend="Hospital" class="ml-3">
							<b-form-select :options="hospitals"/>
						</b-input-group>
						<b-button type="submit" variant="outline-primary" class="ml-2">
							<i class="fa fa-search fa-fw fa-lg"></i>
						</b-button>
						<b-button type="reset" variant="secondary" class="ml-1">
							<i class="fa fa-refresh fa-fw fa-lg"></i>
						</b-button>

						<b-button variant="primary" class="ml-auto" @click="onAddClick">
							<i class="fa fa-plus fa-fw fa-lg"></i>&nbsp;Add
						</b-button>
					</b-form>
					<div class="row">
						<tablegrid v-bind:griddata="this.$store.state.memberlist"></tablegrid>
					</div>
				</b-card>
			</b-col>
		</b-row>
	</div>
</template>

<script>
import tablegrid from "@/shared/grid.vue";
export default {
	name: "Members",
	components: {
		tablegrid
	},
	data: function() {
		return {
			search: {
				name: ""
			},
			showSearch: true,
			IsAddAllowed: true,
			hospitals: [
				{ value: null, text: "Please select..." },
				{ value: "1", text: "National Hospital of Sri Lanka" },
				{ value: "2", text: "Dental Institute" },
				{ value: "3", text: "Colombo South Teaching Hospital" },
				{ value: "4", text: "Lady Ridgeway Hospital for Children" }
			]
		};
	},
	methods: {
		onSubmit(evt) {
			evt.preventDefault();
		},
		onReset(evt) {
			evt.preventDefault();
			this.search.name = "";
			/* Trick to reset/clear native browser form validation state */
			this.showSearch = false;
			this.$nextTick(() => {
				this.showSearch = true;
			});
		},
		onAddClick() {
			this.$router.push("new");
		}
	}
};
</script>